.body {
	/* defaul chinese android and ios fonts */
	font-family: "Noto Sans SC", "PingFang SC";
}

.qrcode {
	background: url(../public/images/repeat_y.png) repeat-y;
	background-size: 100% auto;
}

.bottom {
	position: relative;
}

.top {
	background: url(../public/images/bg_top.png) no-repeat;
	background-size: 100% auto;
}

.empty {
	height: 44vw;
}

.location {
	/* transform: translateY(-20px); */
	width: 214px;
	line-height: 33px;
	font-size: 23px;
	/* margin-top: 44vw; */
  	margin-left: 13.5vw;
	max-height: 66px;
	overflow: hidden;
	color: #323232;
}

.fix {
	width: 28.5vw;
	height: auto;
	/* pointer-events: none; */
	position: fixed;
	right: 0;
	top: 91vw;
}

.bg {
	width: 100vw;
	height: auto;
	pointer-events: none;
}

.time {
	position: absolute;
	top: 58vw;
	left: 50vw;
	transform: translateX(-50%);
	color: #03c74b;
}

.record {
	position: absolute;
	top: 129vw;
	right: 10vw;
	color: #6b6b6b;
}

.gender {
	position: absolute;
	width: 9.6vw;
	top: 32vw;
	left: 13.5vw;
	opacity: 1;
}

.name {
	position: absolute;
	top: 32vw;
	left: 26vw;
	color: #343434;
	font-size: 18px;
}

.id {
	position: absolute;
	top: 37.5vw;
	left: 26vw;
	color: #666666;
}

.period {
	position: absolute;
	top: 51vw;
	left: 69vw;
	color: #03c74b;
}

.open_time {
	position: absolute;
	top: 4vw;
	left: 19vw;
	color: #343434;
	font-size: 18px;
}

.block {
	position: absolute;
	top: 14vw;
	left: 19vw;
	color: #343434;
	font-size: 18px;
}