.body {
  /* defaul chinese android and ios fonts */
  font-family: "Noto Sans SC","PingFang SC";
}

.fix {
	width: 26vw;
	height: auto;
	pointer-events: none;
	position: fixed;
	right: 0;
	top: 70vw;
}

.bg {
	width: 100vw;
	height: auto;
	pointer-events: none;
}

.time {
	position: absolute;
	top: 72vw;
	left: 50vw;
	transform: translateX(-50%);
	color: #03c74b;
}

.record {
	position: absolute;
	top: 194vw;
	right: 10vw;
	color: #6b6b6b;
}

.gender {
	position: absolute;
	width: 9.6vw;
	top: 43.5vw;
	left: 13.5vw;
	opacity: 1;
}

.name {
	position: absolute;
	top: 43vw;
	left: 26vw;
	color: #343434;
	font-size: 18px;
}

.id {
	position: absolute;
	top: 49vw;
	left: 26vw;
	color: #666666;
}

.period {
	position: absolute;
	top: 63vw;
	left: 69vw;
	color: #03c74b;
}